@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@900&display=swap);
.card {
    display: inline-block;
    width: 30%;
    margin: 25px;
    background-color: #ffffff;
    border: 1px solid #bfbfbf;
    border-radius: 5px;
    transition: 0.3s;
}

.card:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    box-shadow: 0px 0px 15px #bfcdd9;
}

.card img {
    width: 100%;
    border-radius: 5px 5px 0px 0px;
}

.card h2 {
    padding-bottom: 5px;
    color: #161616;
}

@media (max-width: 768px) {
    .card {
        width: 80%;
    }
}
body {
    margin: 0;
    padding: 0;
    background-color: #e9eef2;
    font-family: Arial, Helvetica, sans-serif;
    text-align: center;
}

#heading {
    font-family: 'Montserrat', sans-serif;
    background-color: #161616;
    color: #ffffff;
    text-transform: uppercase;
    margin-top: 0;
    padding: 20px;
    cursor: pointer;
}

#gameText {
    color: #00ff80;
}

input[type="button"] {
    border: none;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    background-color: #38a3a5;
    color: #ffffff;
    font-size: 25px;
    margin: 10px 0px;
}

input[type="button"]:hover {
    cursor: pointer;
    background-color: #2e8485;
}

#pageNum {
    margin: 0px 10px;
}

#backToTop {
    color: #005ff9;
}

#backToTop:hover {
    cursor: pointer;
    color: #004ecc;
}
